@font-face {
  font-family: 'Font Awesome 5 Free';
  font-style: normal;
  font-weight: 400;
  font-display: auto;
  src: url('#{$path-fonts}/fa5/fa-regular-400.eot');
  src: url('#{$path-fonts}/fa5/fa-regular-400.eot?#iefix') format('embedded-opentype'), url('#{$path-fonts}/fa5/fa-regular-400.woff2') format('woff2'),
    url('#{$path-fonts}/fa5/fa-regular-400.woff') format('woff'), url('#{$path-fonts}/fa5/fa-regular-400.ttf') format('truetype'),
    url('#{$path-fonts}/fa5/fa-regular-400.svg#fontawesome') format('svg');
}

